html,
body {
  background-color: #f2f2f2;
  height: 100%;
  margin: 0;
  width: 100%;
}
header {
  background: #111111;
}
label {
  font-weight: normal;
  user-select: none;
}
form {
  margin-bottom: 0;
}
div[ng-view] {
  background-color: #f2f2f2;
  height: calc(100% - 72px);
}
hr {
  border-color: #b9b9b9;
}
.title {
  font-size: 52px;
}
.estimation-card {
  width: 550px;
  position: relative;
  top: -50px;
  z-index: 2;
}
.fab-container {
  position: absolute;
  top: 180px;
  left: 285px;
  z-index: 60;
}
.create-form,
.user-form {
  margin-top: 40px;
}
.channel-icon {
  height: 42px;
}
.value-checkbox-wrapper {
  display: inline-block;
  width: 100px;
}
@keyframes blink {
  0% {
    opacity: .2;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: .2;
  }
}
.ticket.selected {
  background-color: rgba(158, 158, 158, 0.4);
}
.estimate-ticket-tab-content,
.estimated-tickets-tab-content {
  background: #f2f2f2;
}
.estimate-ticket-tab-content .current-ticket-footer,
.estimated-tickets-tab-content .current-ticket-footer {
  height: 50px;
  line-height: 48px;
  font-size: 16px;
}
.estimate-ticket-tab-content .current-ticket-footer .current-ticket-text,
.estimated-tickets-tab-content .current-ticket-footer .current-ticket-text {
  padding: 0 6px;
  font-weight: bold;
}
.exportStoryPointsFab {
  position: absolute;
  top: 30px;
  right: 150px;
}
.result-container {
  perspective: 1000px;
  transform-style: preserve-3d;
}
.result-container.flip .card-container .front {
  transform: rotateY(180deg);
}
.result-container.flip .card-container .back {
  transform: rotateY(0deg);
}
.result-container .card-container {
  height: 300px;
  margin: 10px;
  width: 200px;
  float: left;
  position: relative;
}
.result-container .card-container.selected-card .front,
.result-container .card-container.selected-card .back {
  box-shadow: 0 4px 8px 0 #ff0000;
}
.result-container .front,
.result-container .back {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  backface-visibility: hidden;
  transition: 0s;
  transform-style: preserve-3d;
  position: absolute;
  top: 0;
  left: 0;
}
.result-container .front:hover,
.result-container .back:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.result-container .front .card-value,
.result-container .back .card-value {
  color: #f2f2f2;
  border-radius: 5px 5px 0 0;
  display: table;
  font-size: 72px;
  font-weight: bold;
  height: 80%;
  text-align: center;
  background-color: #3f51b5;
  width: 100%;
}
.result-container .front .card-value > span,
.result-container .back .card-value > span {
  display: table-cell;
  vertical-align: middle;
}
.result-container .front .user-name,
.result-container .back .user-name {
  font-weight: bold;
  height: 20%;
  padding: 10% 0;
  text-align: center;
}
.result-container .front {
  z-index: 2;
  transform: rotateY(0deg);
}
.result-container .back {
  transform: rotateY(-180deg);
}
.result-container .back span {
  font-size: 72px;
}
.result-container .dots {
  margin: 50px 0;
}
.result-container .dots span {
  animation-duration: 1.4s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  animation-name: blink;
}
.result-container .dots span:nth-child(2) {
  animation-delay: .2s;
}
.result-container .dots span:nth-child(3) {
  animation-delay: .4s;
}
.set-story-point-dialog .sp {
  min-width: 52px;
  display: inline-block;
  padding: 12px;
  margin: 6px;
  text-align: center;
  box-sizing: border-box;
  color: white;
  font-size: 20px;
  background: #3f51b5;
  border: 2px solid transparent;
  border-radius: 16%;
}
.set-story-point-dialog .sp:focus {
  outline: none;
}
.set-story-point-dialog .sp.selected {
  background: #606fc7;
  border: 2px solid #3f51b5;
}
md-toast.md-error-theme div.md-toast-content {
  color: #a94442 !important;
  background-color: #f2dede !important;
}
md-toast.md-warning-theme div.md-toast-content {
  color: #8a6d3b !important;
  background-color: #fcf8e3 !important;
}
md-toast.md-success-theme div.md-toast-content {
  color: #3c763d !important;
  background-color: #dff0d8 !important;
}
md-tabs.stretch-height md-tab-content > div {
  margin: 0;
  width: 100%;
  min-height: 100%;
  height: 100%;
}
.md-subheader {
  background-color: #ffffff;
}
.card-pick-container {
  padding: 6px;
  text-align: center;
  user-select: none;
}
.card-pick-container .card {
  background-color: #f2f2f2;
  border-radius: 5px;
  color: #f2f2f2;
  cursor: pointer;
  display: inline-block;
  font-size: 120px;
  height: 192px;
  line-height: 176px;
  margin: 10px;
  text-align: center;
  width: 176px;
}



